.tasks {

    &:not(:last-of-type) {
        margin-bottom: 40px;
    }

    &__title {
        font-family: Montserrat;
        font-weight: bold;
        font-size: 32px;
        line-height: 39px;
        color: #64c4ed;
        padding-bottom: 20px;
        border-bottom: 1px solid #f2f2f2;
        &:hover {
            img {
                opacity: 0.2;
            }
        }
        img {
            opacity: 0;
            cursor: pointer;
            margin-left: 15px;

            &:hover {
                opacity: 0.8;
            }
        }
    
    }
    &__form {
        margin-top: 20px;

        &-block {
            .button {
                margin-right: 10px;
                margin-top: 15px;
            }
        }
        &-new {
            display: flex;
            align-items: center;
            cursor: pointer;
            opacity: 0.4;
            transition: opacity 0.15s ease-in-out;

            &:hover {
                opacity: 0.8;
            }
            

            img {
                width: 16px;
                height: 16px;
                margin-left: 4px;
            }

            span {
                font-size: 16px;
                margin-left: 19px;

            }
        }
    }
    &__item{
        margin-top: 30px;

        h2 {
            font-family: Montserrat;
            font-weight: 700;
            font-size: 32px;
            color: #C9D1D3;
            text-align: center;
            position: absolute;
            top: 50%;
            left: 50%;
        }

        &-row {
            display: flex;
            align-items: center;
            margin-bottom: 15px;

            &:hover &-actions {
                opacity: 1;
            }
            &-actions {
                display: flex;
                opacity: 0;
                transition: opacity 0.15s ease-in-out;

                div {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 32px;
                    height: 32px;
                    background-color: lighten(#f4f6f8, 2%);
                    border-radius: 6px;
                    margin-left: 5px;
                    cursor: pointer;
                    

                    &:hover {
                        background-color: darken(#f4f6f8, 2%);
                        svg {
                            opacity: 0.9;
                        }
                    }
                    svg {
                        width: 11px;
                        height: 11px;
                        opacity: 0.4;
                    }
                }
            }
            

            input {
                width: 100%;
                border: 0;
                margin-left: 15px;
                font-size: 16px;
            }
        }
    }
    

    .checkbox {
        
        input {
            display: none;
        }

        svg {
            path {
                stroke: #f2f2f2;
            }
        }

        &:hover {
            label {
                background-color: #f2f2f2;
                border-color: #f2f2f2;
                svg {
                    opacity: 1;
                    path {
                        stroke: #b2b2b2;
                    }
                }
            }
        }
        label {
            display: flex;
            align-items: center;
            justify-content: center;
            border: 2px solid #e8e8e8;
            border-radius: 30px;
            width: 20px;
            height: 20px;
            cursor: pointer;

            svg {
                opacity: 0;
            }
        }
        input:checked + label {
            background-color: #4dd599;
            border-color: #4dd599;

            svg {
                opacity: 1;
                path {
                    stroke: #fff;
                }
            }
        }  
    }
}